@import url("https://fonts.googleapis.com/css2?family=Maven+Pro&display=swap");

body {
	font-family: "Maven Pro", sans-serif;
}

body {
	background-color: #eee;
}

.add {
	border-radius: 20px;
}

.card {
	border: none;
	border-radius: 10px;
	transition: all 1s;
	cursor: pointer;
}

.card:hover {
	-webkit-box-shadow: 3px 5px 17px -4px #777777;
	box-shadow: 3px 5px 17px -4px #777777;
}

.ratings i {
	color: green;
}

.apointment button {
	border-radius: 20px;
	background-color: rgb(17, 233, 53);
	color: #000;
	border-color: #eee;
	font-size: 13px;
	font-weight: 700;
}
.img {
	width: 5rem !important;
	height: 5rem !important;
	border-radius: 10px;
	margin: auto;
}
